@import '/src/styles/colors'; 

@import '/src/assets/01_style/01_layout/layoutType02.scss';


.container {


  //nav
  .header {
    height: 50px;

    .tabs {
      align-items: center; /* 가운데 정렬 */
      display: flex;
      width: 100%;
      height: 100%;
      flex-direction: row; /* 세로로 정렬 */
      :hover{
        background-color: darken($colorBg , 20);
        text-decoration: underline;
      }
    
      .tab {
        display: flex;
        align-items: center; /* 세로 중앙 정렬 */
        justify-content: center; /* 가로 중앙 정렬 */
        height: 100%;
        color: $colorTxt;
        padding: 10px;

        cursor: pointer;
        text-decoration: none;
      }
    
    }
    @media (max-width: 768px) {
      height: 40px;
    }
  }


  .content{

    .content1 {
      width: 70%;
    }
    .content2 {
    }
  }

	@media (max-width: 768px) {
		.content {
			flex-direction: column; /* nav를 아래로 이동 */
	
			.content1 {
				width: 100%;
				height: 70%; /* 높이 설정 */
			}
	
			.content2 {
				width: 100%; 
				order: 2; /* main-content를 nav 아래로 */
				flex: 1;
			}
		}
	}


}
