
    /*
    container 형태
    |-------------------------| 
    |           header        |
    |-------------------------|
    |        (content)        |
    |             |           | 
    | content1    | content2  |
    |             |           |
    |-------------------------|
    */
    
.container {
  display: flex;
  flex-direction: column;
  width: 100%; /* 부모 div의 너비에 맞춤 */
  height: 100%; /* 부모 div의 높이에 맞춤 */
  //background-color: #833;

  .header {
    height: 8%;
  }

  .content {
    display: flex;
    flex: 1;

    .content1 {
      width: 15%;
      height: 100%;
    }

    .content2 {
      flex: 1;
      height: 100%;
      overflow: auto;
    }
  }

}


