$colorBg: #223355;

.container {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: $colorBg;

  .header {
    height: 50px; /* 헤더 높이 */
    background-color: darken($colorBg, 10);
    display: flex;

    padding: 0 10px; /* 좌우 여백 */

    .buttons {
      display: flex;
      gap: 10px; /* 버튼 사이 간격 */
      margin-left: auto; /* 오른쪽 정렬 */
      align-items: center; /* 세로 중앙 정렬 */
      justify-content: center; /* 가로 중앙 정렬 */

      .button {
        display: flex;
        border: none;
        cursor: pointer;
        align-items: center; /* 세로 중앙 정렬 */
        justify-content: center; /* 가로 중앙 정렬 */
        height: 40px;
        border-radius: 4px; /* 버튼 모서리 둥글게 */
  
        &:hover {
          background-color: lighten($colorBg, 10); 
          color: darken($colorBg, 20); 
        }
  
        img {
          width: 24px; /* 아이콘 크기 */
          height: 24px;
        }
      }
    }
  }


  .fileList {

    flex-grow: 1;
    height: 0;
    overflow: scroll; /* 스크롤이 가능하도록 설정 */
    cursor: pointer;
    color: lighten($colorBg, 50); 
    .lineDir {
      width: 100%; /* 부모 요소의 너비를 100%로 맞춤 */
      display: flex;
      height: 50px; /* 고정된 높이 */
      //background-color: #333;
      align-items: center;
      white-space: nowrap; /* 줄 바꿈 방지 */

      .icon {
        width: 16px;
        height: 16px;
        margin-right: 8px;
      }
    }

    .lineDir:hover {
      background-color: darken($colorBg, 20);
      color: lighten($colorBg, 20); 
    }
  }
  
}








